import React from "react";
import styled from "styled-components";
import { motion } from "framer-motion";

import LinkAnimation from "../Animations/LinkAnimation";
import cyper_music from "../../Assests/cyper-music.png";
import praise_home from "../../Assests/praise-home.png";

const BlogContainer = styled.div`
  width: 100%;
  min-height: 100vh;
  height: 100%;

  .links {
    padding: 2rem 1rem;
    border-top: 0.5px solid #aaaaaa50;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    min-height: 80px;
    gap: 10vw;
    transition: all 0.5s ease-in-out;

    a {
      text-decoration: none;
    }

    @media screen and (max-width: 768px) {
      gap: 1rem;
      padding: inherit 0;
    }
  }
`;

function Blog() {
  return (
    <BlogContainer>
      <motion.div
        initial={{
          opacity: 0,
          x: 200,
        }}
        whileInView={{
          opacity: 1,
          x: 0,
        }}
        transition={{
          duration: 0.5,
          ease: "easeInOut",
          repeat: false,
        }}
        className="links"
      >
        <LinkAnimation
          text="Tesla Clone with React Js And Several React Libraries"
          fontsize="1.5rem"
          fontweight="500"
          color="#fff"
          hovercolor="#eee"
          img_src="https://dennissnellenberg.com/media/pages/work/base-create/6f57a8fab4-1680180571/case-header-base-v2.jpg"
          src="/cases/cypercase"
        />
      </motion.div>
      <motion.div
        initial={{
          opacity: 0,
          x: 200,
        }}
        whileInView={{
          opacity: 1,
          x: 0,
        }}
        transition={{
          duration: 0.5,
          ease: "easeInOut",
          delay: 0.5,
          repeat: false,
        }}
        className="links"
      >
        <LinkAnimation
          text="Cyper Music - Online Music Listening Website"
          fontsize="1.5rem"
          fontweight="500"
          color="#fff"
          hovercolor="#eee"
          img_src={cyper_music}
          src="/tesla-clone"
        />
      </motion.div>
      <motion.div
        initial={{
          opacity: 0,
          x: 200,
        }}
        whileInView={{
          opacity: 1,
          x: 0,
        }}
        transition={{
          duration: 0.5,
          ease: "easeInOut",
          delay: 0.5,
          repeat: false,
        }}
        className="links"
      >
        <LinkAnimation
          text="Praise - A Blog Website"
          fontsize="1.5rem"
          fontweight="500"
          color="#fff"
          hovercolor="#eee"
          img_src={praise_home}
          src="/tesla-clone"
        />
      </motion.div>
    </BlogContainer>
  );
}

export default Blog
