import React, { useEffect, useState } from "react";
import Cubertovideo from "../../Assests/video/cuberto-video.mp4";
import styled from "styled-components";
import { motion } from "framer-motion";

const Container = styled.div`
  width: 100vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3rem 0;

  @media screen and (max-width: 768px) {
    margin-top: 0;
  }
`;

const StyledVideo = styled(motion.video)`
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 500px;
  transition: all 0.5s ease-in-out;
  animation: BorderRadius 5s infinite ease-in-out;
  &::-webkit-media-controls-panel {
    display: none !important;
    -webkit-appearance: none;
  }

  @keyframes BorderRadius {
    0% {
      border-radius: 500px;
    }

    25% {
      border-radius: 350px;
    }

    50% {
      border-radius: 200px;
    }

    75% {
      border-radius: 350px;
    }
  }
`;

function Video() {
  return (
    <div>
      <Container>
        <StyledVideo
          initial={{
            width: "400px",
            height: "400px",

            y: -100,
          }}
          whileInView={{
            width: "80vw",
            height: "80vh",

            y: 0,
            x: 0,
            transition: {
              delay: 0.2,
              duration: 1,
              type: "just",
              stiffness: 100,
              damping: 10,
              ease: "linear",
            },
          }}
          autoPlay
          loop
          muted
          controls
          src={Cubertovideo}
        ></StyledVideo>
      </Container>
    </div>
  );
}

export default Video;
